import { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom';
import Button from '../../components/Button/Button'
import { PRIMARY } from '../../utils/Constants';
import useConfig from '../../hooks/useConfig'
import { useAlert } from 'react-alert'
import Spinner from './Spinner'
import BSC from '../../assets/img/bsc-logo.svg';
import ETH from '../../assets/img/ethereum.svg'
import HECO from '../../assets/img/heco.svg'
import stageOne from './img/stage-one.svg'
import stageTwo from './img/stage-two.svg'
import stageThree from './img/stage-three.svg'
import apiProxy from '../../model/ApiProxy';
import succeed from './img/succeed.svg'
import pending from './img/pending.svg'
export default function Operate({ lang, wallet, amount, balance, initialize, isClaim, setSending, sending, setAmount, setIsClaim, isWalletConnected }) {
  const config = useConfig();
  const alert = useAlert();
  const history = useHistory();
  const [isApprove, setIsApprove] = useState(false);
  const [isValid, setIsValid] = useState(false);
  const [bridgeState, setBridgeState] = useState({});
  const [claimState, setClaimState] = useState({});
  const [isClickClaim, setIsClickClaim] = useState(false);
  const [actionElement, setActionElement] = useState(<Button type={PRIMARY} className='button-bridge' fontSize='18' label={lang['approve']} ></Button>);
  const connect = () => {
    wallet.connect()
  }
  const valid = async () => {
    if (wallet.isConnected()) {
      let res = await apiProxy.request('getUserWormholeSignature', [wallet.detail.account]);
      if (res.valid) {
        wallet.switchNetwork(config[res.toChainId])
        setIsClaim(true)

      }
      setIsValid(res.valid)
    }
  }
  const send = async () => {
    if (amount === '' || amount === 0) {
      alert.error(lang['amount-must-be-greater-than-zero'])
      return;
    }
    if (+amount > balance) {
      alert.error(lang['there-is-not-enough-amount'])
      return;
    }
    if (!isWalletConnected.isFromConnected) {
      alert.error(`${lang['send-finished-one']} ${isNetwork(initialize.from_chainId, lang).netWork} ${lang['send-finished-two']}`)
      return;
    }
    setSending(true)
    setBridgeState({
      stageOne: false
    })
    let res = await apiProxy.request('freeze', [wallet.detail.chainId, wallet.detail.account, initialize.to_chainId, amount], { includeResponse: true })
    if (res.success) {
      setActionElement(<Button type={PRIMARY} className='button-bridge' label={lang['processing']} />)
      setBridgeState({
        stageOne: true,
        stageTwo: false
      })
      window.setInterval(() => {
        valid()
      }, 3000)
    } else {
      setSending(false)
    }
  }
  const claim = async () => {
    if (!isWalletConnected.isToConnected) {
      alert.error(`${lang['send-finished-one']} ${isNetwork(initialize.to_chainId, lang).netWork} ${lang['send-finished-two']}`)
      return;
    }
    setIsClickClaim(true)
    setClaimState({
      stageOne: false,
    })
    let res = await apiProxy.request('mintDeri', [wallet.detail.chainId, wallet.detail.account], { includeResponse: true })
    if (res.success) {
      setClaimState({
        stageOne: true,
        stageTwo: false,
      })
      setActionElement(<Button type={PRIMARY} className='button-bridge' label={lang['processing']} />)
      window.setInterval(() => {
        valid()
      }, 3000)
    } else {
      alert.error(lang['claim-faild'])
    }
  }
  const reset = ()=>{
    history.push('/bridge')
  }
  const approve = async () => {
    const res = await apiProxy.request('unlockDeri', [wallet.detail.chainId, wallet.detail.account], { includeResponse: true })
    if (res.success) {
      setIsApprove(true);
      loadApprove();
    } else {
      setIsApprove(false)
      alert.error(lang['approve-faild'])
    }
  }
  const loadApprove = async () => {
    if (wallet.isConnected()) {
      const result = await apiProxy.request('isDeriUnlocked', [wallet.detail.chainId, wallet.detail.account])
      setIsApprove(result);
    }
  }
  useEffect(() => {
    if (wallet.isConnected()) {
      loadApprove();
      valid()
    }
  }, [wallet.detail, initialize]);
  useEffect(() => {
    if (isValid) {
      setBridgeState({
        stageOne: true,
        stageTwo: true,
        stageThree: true,
      })
    } else if (isClickClaim) {
      setClaimState({
        stageOne: true,
        stageTwo: true,
        stageThree: true,
      })
      setSending(false)
      setAmount('')
    } else {
    }
  }, [isValid, initialize]);
  useEffect(() => {
    let element;
    if (wallet.isConnected()) {
      if (isValid) {
        element = <Button type={PRIMARY} className='button-bridge' label={lang['claim']} onClick={claim} />
      } else if (!isApprove) {
        element = <Button type={PRIMARY} className='button-bridge' label={lang['approve']} onClick={approve} />
      } else if(isClickClaim){
        element = <button className='complete' onClick={reset}> <img alt='' src={succeed}/> {lang['complete']} </button>
      }else {
        element = <Button type={PRIMARY} className='button-bridge' label={lang['send']} onClick={send} ></Button>
      }
    } else {
      element = <Button type={PRIMARY} className='button-bridge' label={lang['connect-wallet']} onClick={connect} ></Button>
    }
    setActionElement(element)
  }, [isValid, wallet.detail, isApprove, amount, initialize, isWalletConnected,isClickClaim])
  return (
    <div className='operate'>
      <div className='from-to-title'>
        {lang['bridge']} {amount}  DERI {lang['from'].toLowerCase()} {initialize.from_network} {lang['to'].toLowerCase()} {initialize.to_network}
      </div>
      <div className='btn'>
        {actionElement}
      </div>
      <div className='hint'>
        {!isClaim && <div className='sending'>
          <div className='stage'>
            <div className='stage-img-text'>
              <div className='stage-img'> <img alt='' src={stageOne} /></div>
              <div className='stage-text'>{lang['sending-deri-to-wormhole']} </div>
            </div>
            <div className='ok-pending'>
              {sending && <>
                {bridgeState.stageOne ? <div className='succeed'>
                  <img alt='' src={succeed} />
                </div> : <div className='pending'>
                    <Spinner />
                  </div>}
              </>}
              {!sending && <div className='staic'></div>}
            </div>
          </div>
          <div className='stage'>
            <div className='stage-img-text'>
              <div className='stage-img'> <img alt='' src={stageTwo} /></div>
              <div className='stage-text'>{lang['waiting-for-bridge-to-sign']}</div>
            </div>
            <div className='ok-pending'>
              {sending && <>
                {bridgeState.stageOne && <>
                  {bridgeState.stageTwo ? <div className='succeed'>
                    <img alt='' src={succeed} />
                  </div> : <div className='pending'>
                      <Spinner />
                    </div>}
                </>}
              </>}
              {(!sending || !bridgeState.stageOne) && <div className='staic'></div>}
            </div>
          </div>
          <div className='stage'>
            <div className='stage-img-text'>
              <div className='stage-img'> <img alt='' src={stageThree} /></div>
              <div className='stage-text'>{lang['bridge-signed']}</div>
            </div>
            <div className='ok-pending'>
              {sending && <>
                {bridgeState.stageOne && bridgeState.stageTwo && <>
                  {bridgeState.stageThree ? <div className='succeed'>
                    <img alt='' src={succeed} />
                  </div> : <div className='pending'>
                      <Spinner />
                    </div>}
                </>}

              </>}
              {(!sending || !bridgeState.stageTwo) && <div className='staic'></div>}
            </div>
          </div>
        </div>}
        {isClaim && <div className='claiming'>
          <div className='stage'>
            <div className='stage-img-text'>
              <div className='stage-img'> <img alt='' src={stageOne} /></div>
              <div className='stage-text'>{lang['claim-deri-ing']}</div>
            </div>
            <div className='ok-pending'>
              {isClickClaim && <>
                {claimState.stageOne ? <div className='succeed'>
                  <img alt='' src={succeed} />
                </div> : <div className='pending'>
                    <Spinner />
                  </div>}
              </>}
              {!isClickClaim && <div className='staic'></div>}
            </div>
          </div>
          <div className='stage'>
            <div className='stage-img-text'>
              <div className='stage-img'> <img alt='' src={stageTwo} /></div>
              <div className='stage-text'>{lang['waiting-for-bridge-to-sync']} </div>
            </div>
            <div className='ok-pending'>
              {isClickClaim && <>
                {claimState.stageOne && <>
                  {claimState.stageTwo ? <div className='succeed'>
                    <img alt='' src={succeed} />
                  </div> : <div className='pending'>
                      <Spinner />
                    </div>}
                </>}
              </>}
              {(!isClickClaim || !claimState.stageOne) && <div className='staic'></div>}
            </div>
          </div>
          <div className='stage'>
            <div className='stage-img-text'>
              <div className='stage-img'> <img alt='' src={stageThree} /></div>
              <div className='stage-text'>{lang['finished']}</div>
            </div>
            <div className='ok-pending'>
              {isClickClaim && <>
                {claimState.stageOne && claimState.stageTwo && <>
                  {claimState.stageThree ? <div className='succeed'>
                    <img alt='' src={succeed} />
                  </div> : <div className='pending'>
                      <Spinner />
                    </div>}
                </>}

              </>}
              {(!isClickClaim || !claimState.stageTwo) && <div className='staic'></div>}
            </div>
          </div>
        </div>}
      </div>
    </div>
  )
}
function isNetwork(chainId, lang) {
  chainId = +chainId
  let obj = {}
  switch (chainId) {
    case 1:
      obj.netWork = lang["ethereum"];
      obj.chainId = 1;
      obj.img = ETH;
      break;
    case 56:
      obj.netWork = lang["bsc"];
      obj.chainId = 56;
      obj.img = BSC;
      break;
    case 128:
      obj.netWork = lang["heco"];
      obj.chainId = 128;
      obj.img = HECO;
      break;
    case 97:
      obj.netWork = lang["bsc-testnet"];
      obj.chainId = 97;
      obj.img = BSC;
      break;
    case 3:
      obj.netWork = lang["ropsten"];
      obj.chainId = 3;
      obj.img = ETH;
      break;
    case 256:
      obj.netWork = lang["heco-testnet"];
      obj.chainId = 256;
      obj.img = HECO;
      break;
    default:
      break;
  }
  return obj;
}