import React, { useState, useEffect } from 'react'
import { inject, observer } from 'mobx-react';
import { DeriEnv, bg } from "../../lib/web3js/index";
import Input from '../../components/Input/Input'
import Operate from './Operate'
import DeriNumberFormat from '../../utils/DeriNumberFormat'
import apiProxy from '../../model/ApiProxy'
import arrow from './img/arrow-left.svg'
import disabledDown from './img/disabled-down.svg'
import BSC from '../../assets/img/bsc-logo.svg';
import ETH from '../../assets/img/ethereum.svg'
import deriLogo from '../../assets/img/logo.svg'
import HECO from '../../assets/img/heco.svg'
import down from '../../assets/img/arrow-down.svg'
import classNames from 'classnames' 
import './bridge.less'
function Bridge({ wallet, lang }) {
  const [amount, setAmount] = useState('');
  const [balance, setBalance] = useState(0);
  const isdev = DeriEnv.get() !== 'prod' ? true : false;
  const netWork_text = isdev ?
    [
      {
        text: lang['ropsten'],
        id: 3,
        img: ETH,
      },
      {
        text: lang['bsc-testnet'],
        id: 97,
        img: BSC,
      },
      {
        text: lang['heco-testnet'],
        id: 256,
        img: HECO,
      }
    ]
    : [

      {
        text: lang['ethereum'],
        id: 1,
        img: ETH,
      },
      {
        text: lang['bsc'],
        id: 56,
        img: BSC,
      },
      {
        text: lang['heco'],
        id: 128,
        img: HECO,
      }

    ]

  const [fromList, setFromList] = useState(netWork_text)
  const [toList, setToList] = useState(netWork_text)
  const [sending, setSending] = useState(false);
  const [initialize, setInitialize] = useState(isdev ?
    {
      from_chainId: 97,
      from_network: lang['bsc-testnet'],
      to_chainId: 3,
      to_network: lang['ropsten']
    } : {
      from_chainId: 1,
      from_network: lang['ethereum'],
      to_chainId: 56,
      to_network: lang['bsc']
    });
  const [isClaim, setIsClaim] = useState(false);
  const [dropdownList_from, setDropdownList_from] = useState(false);
  const [dropdownList_to, setDropdownList_to] = useState(false);
  const [From_img, setFrom_img] = useState(isNetwork(initialize.from_chainId, lang).img);
  const [To_img, setTo_img] = useState(isNetwork(initialize.to_chainId, lang).img);
  const selectListClassFrom = classNames('from-network-list', { 'show': dropdownList_from })
  const selectListClassTo = classNames('to-network-list', { 'show': dropdownList_to })
  const [isWalletConnected, setIsWalletConnected] = useState(
    {
      isFromConnected: false,
      isToConnected: false
    }
  );
  const isConnected = () => {
    let isFromConnected = wallet.isConnected() ? (+wallet.detail.chainId === +initialize.from_chainId ? true : false) : false;
    let isToConnected = wallet.isConnected() ? (+wallet.detail.chainId === +initialize.to_chainId ? true : false) : false;
    let obj = {
      isFromConnected: isFromConnected,
      isToConnected: isToConnected
    }
    setIsWalletConnected(obj)
  }

  const showListFrom = () => {
    if (sending) {
      return;
    }
    setDropdownList_from(!dropdownList_from)
  }
  const showListTo = () => {
    if (sending) {
      return;
    }
    setDropdownList_to(!dropdownList_to)
  }

  const getValid = async () => {
    if (wallet.isConnected()) {
      let res = await apiProxy.request('getUserWormholeSignature', [wallet.detail.account]);
      if (res.valid) {
        setInitialize({
          from_chainId: res.fromChainId,
          from_network: isNetwork(res.fromChainId, lang).netWork,
          to_chainId: res.toChainId,
          to_network: isNetwork(res.toChainId, lang).netWork,
        })
        setFrom_img(isNetwork(res.fromChainId, lang).img)
        setTo_img(isNetwork(res.toChainId, lang).img)
        setAmount(bg(res.amount, -18).toString())
        setSending(res.valid)
      }
    }
  }

  const getBalance = async () => {
    if (wallet.isConnected()) {
      let res = await apiProxy.request('getDeriBalance', [wallet.detail.chainId, wallet.detail.account])
      setBalance(res)
    }
  }

  useEffect(() => {
    getBalance()
  }, [wallet.detail, initialize]);

  useEffect(() => {
    getValid()
  }, [wallet.detail])

  const select_from = obj => {
    let Init = JSON.parse(JSON.stringify(initialize));
    if (Init.to_chainId === obj.id) {
      Init.from_chainId = initialize.to_chainId;
      Init.from_network = initialize.to_network;
      Init.to_chainId = initialize.from_chainId;
      Init.to_network = initialize.from_network;
    } else {
      Init.from_chainId = obj.id;
      Init.from_network = obj.text;
    }
    setInitialize(Init)
    setDropdownList_from(!dropdownList_from)
  }
  const select_to = obj => {
    let Init = JSON.parse(JSON.stringify(initialize));
    if (Init.from_chainId === obj.id) {
      Init.from_chainId = initialize.to_chainId;
      Init.from_network = initialize.to_network;
      Init.to_chainId = initialize.from_chainId;
      Init.to_network = initialize.from_network;
    } else {
      Init.to_chainId = obj.id;
      Init.to_network = obj.text;
    }
    setInitialize(Init)
    setDropdownList_to(!dropdownList_to)

  }

  useEffect(() => {
    let from_arr;
    let to_arr;
    let from_one = netWork_text.filter(p => p.id === +initialize.from_chainId)[0]
    let from_two = netWork_text.filter(p => p.id !== +initialize.from_chainId)[0]
    let from_three = netWork_text.filter(p => p.id !== +initialize.from_chainId)[1]
    let to_one = netWork_text.filter(p => p.id === +initialize.to_chainId)[0]
    let to_two = netWork_text.filter(p => p.id !== +initialize.to_chainId)[0]
    let to_three = netWork_text.filter(p => p.id !== +initialize.to_chainId)[1]
    from_arr = [from_one, from_two, from_three]
    to_arr = [to_one, to_two, to_three]
    setFromList(from_arr)
    setToList(to_arr)
    setFrom_img(isNetwork(initialize.from_chainId, lang).img)
    setTo_img(isNetwork(initialize.to_chainId, lang).img)
    isConnected()

  }, [initialize,wallet.detail])

  const getAmount = (value) => {
    setAmount(value)
  }

  return (
    <div className='bridge-big-box'>
      <div className='bridge-box'>
        <div className='bridge-title'>
          <div className='title'>
            {lang['bridge']}
          </div>
          <div className='birdge-des'>
            {lang['bridge-des']}
          </div>
        </div>
        <div className='bridge-info'>
          <div className='select-network-box'>
            <div className='select-network-title'>
              {lang['select-networks']}
            </div>
            <div className='select-network-from-to'>
              <div className='from-network'>
                <div className='from-title'>
                  {lang['from']}
                </div>
                <div className='from-dropdown-list'>
                  <div className={sending ? "drop drop-disabled" : "drop"} onClick={() => { showListFrom() }}>
                    <div className='network-logo-connected'>
                      <img src={From_img} alt='' />
                      <span>{initialize.from_network}</span>
                      <div className={isClaim ? 'connected-hide' : !isWalletConnected.isFromConnected ? 'is-connected' : 'connected'}>
                        {(!isClaim && isWalletConnected.isFromConnected) ? lang['connected'] : lang['unconnected']}
                      </div>
                    </div>
                    <img className='down-icon' src={sending ? disabledDown : down} alt='' />
                  </div>
                  <ul className={selectListClassFrom}>
                    <li className='wallet-li' onClick={() => select_from(fromList[0])}>
                      <div className='li-logo-network'>
                        <img alt='' src={fromList[0].img} /> <span> {fromList[0].text} </span>
                      </div>
                      <div className='up-icon-btn' onClick={() => { showListFrom() }}>
                        <img alt='' src={down} className='up-icon' />
                      </div>
                    </li>
                    <li className='wallet-li' onClick={() => select_from(fromList[1])}>
                      <div className='li-logo-network'>
                        <img alt='' src={fromList[1].img} /> <span> {fromList[1].text} </span>
                      </div>
                    </li>
                    <li className='wallet-li' onClick={() => select_from(fromList[2])}>
                      <div className='li-logo-network'>
                        <img alt='' src={fromList[2].img} /> <span> {fromList[2].text} </span>
                      </div>
                    </li>
                  </ul>
                </div>
              </div>
              <div className='arrow'>
                <img src={arrow} alt='' />
              </div>
              <div className='to-network'>
                <div className='to-title'>
                  {lang['to']}
                </div>
                <div className='to-dropdown-list'>
                  <div className={sending ? "drop drop-disabled" : "drop"} onClick={() => { showListTo() }}>
                    <div className='network-logo-connected'>
                      <img src={To_img} alt='' />
                      <span>{initialize.to_network}</span>
                      <div className={!isClaim ? 'connected-hide' : !isWalletConnected.isToConnected ? 'is-connected' : 'connected'}>
                        {(isClaim && isWalletConnected.isToConnected) ? lang['connected'] : lang['unconnected']}
                      </div>
                    </div>
                    <img className='down-icon' src={sending ? disabledDown : down} alt='' />
                  </div>
                  <ul className={selectListClassTo}>
                    <li className='wallet-li' onClick={() => select_to(toList[0])}>
                      <div className='li-logo-network'>
                        <img alt='' src={toList[0].img} /> <span> {toList[0].text} </span>
                      </div>
                      <div className='up-icon-btn' onClick={() => { showListTo() }}>
                        <img alt='' src={down} className='up-icon' />
                      </div>
                    </li>
                    <li className='wallet-li' onClick={() => select_to(toList[1])}>
                      <div className='li-logo-network'>
                        <img alt='' src={toList[1].img} /> <span> {toList[1].text} </span>
                      </div>
                    </li>
                    <li className='wallet-li' onClick={() => select_to(toList[2])}>
                      <div className='li-logo-network'>
                        <img alt='' src={toList[2].img} /> <span> {toList[2].text} </span>
                      </div>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
          <div className='set-amount'>
            <div className='set-amount-title'>
              {lang['set-amount']}
            </div>
            <div className='set-amount-box'>
              <div className='input-deri'>
                <Input placeholder='0' value={amount} onChange={getAmount} disabled={sending} className='input-box' />
                <span><img alt='' src={deriLogo} className='amount-img' /> DERI</span>
              </div>
              <div className='total-deri'>
                {lang['total-amount']} <DeriNumberFormat value={balance} decimalScale={4} thousandSeparator={true}  /> DERI
              </div>
            </div>
          </div>
          <div className='bridge-operate-hint'>
            <Operate lang={lang} sending={sending} wallet={wallet} balance={balance} isWalletConnected={isWalletConnected} amount={amount} isClaim={isClaim} initialize={initialize} setIsClaim={setIsClaim} setSending={setSending} setAmount={setAmount} />
          </div>
          <div className='bridge-to-polygon'>
            <div>
              <div className='birdge-to-polygon-title'>
                {lang['bridge-to-polygon']} <span className='polygon-title'>
                  <a rel='noreferrer' target='_blank' href='https://wallet.matic.network/bridge'>Polygon</a>
                </span>
              </div>
              <div className='bridge-to-polygon-des'>
                {lang['bridge-to-polygon-des']}
              </div>
            </div>

          </div>
        </div>
      </div>
    </div>
  )
}
function isNetwork(chainId, lang) {
  chainId = +chainId
  let obj = {}
  switch (chainId) {
    case 1:
      obj.netWork = lang["ethereum"];
      obj.chainId = 1;
      obj.img = ETH;
      break;
    case 56:
      obj.netWork = lang["bsc"];
      obj.chainId = 56;
      obj.img = BSC;
      break;
    case 128:
      obj.netWork = lang["heco"];
      obj.chainId = 128;
      obj.img = HECO;
      break;
    case 97:
      obj.netWork = lang["bsc-testnet"];
      obj.chainId = 97;
      obj.img = BSC;
      break;
    case 3:
      obj.netWork = lang["ropsten"];
      obj.chainId = 3;
      obj.img = ETH;
      break;
    case 256:
      obj.netWork = lang["heco-testnet"];
      obj.chainId = 256;
      obj.img = HECO;
      break;
    default:
      break;
  }
  return obj;
}
export default inject('wallet')(observer(Bridge))